h1 {
  font-family: 'Playfair Display', serif; /* Elegant serif font */
  font-size: 2em; /* Large font size for impact */
  text-align: center; /* Center the heading */
  color: #D35400; /* A warm, festive color (e.g., copper or bronze) */
  margin-top: 50px; /* Add space above */
  margin-bottom: 20px; /* Add space below */
  letter-spacing: 2px; /* Slight spacing between letters */
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2); /* Subtle text shadow for depth */
  background: linear-gradient(to right, #c96d6d, #c21022); /* Gradient effect */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; /* Fill text with gradient */
}

h1::after {
  content: "";
  display: block;
  width: 150px;
  height: 3px;
  background-color: #D35400; /* Same as text color */
  margin: 20px auto 0 auto; /* Center the line below the text */
}