/* General Styles */
body {
  font-family: 'Playfair Display', serif;
  background-color: #f7f7f7;
  margin: 0;
  padding: 0;
}

h1 {
  color: #8b0000;
  text-align: center;
  font-size: 2.5rem;
}

h2 {
  color: #8b0000;
  text-align: center;
  font-size: 2rem;
}

.dashboard {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

nav {
  margin: 20px 0;
  display: flex;
  justify-content: space-around; /* Ensure space between tiles */
  gap: 15px;
  flex-wrap: wrap; /* Allow wrapping if there’s not enough space */
}

nav a {
  display: inline-block;
  padding: 20px 30px; /* Increase padding for larger tiles */
  color: white;
  background-color: #8b0000;
  text-decoration: none;
  font-size: 1.2rem;
  border-radius: 5px;
  transition: background-color 0.3s;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow for tile effect */
}

nav a:hover {
  background-color: #b22222;
  transform: translateY(-3px); /* Hover effect to lift the tile slightly */
}

/* Media Query for Mobile (max-width: 768px) */
@media (max-width: 768px) {
  nav {
    flex-direction: column; /* Stack tiles vertically on small screens */
    gap: 10px;
  }

  h1 {
    font-size: 1.8rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  nav a {
    padding: 12px 20px; /* Adjust padding for smaller screens */
    font-size: 1rem;
  }

  .container {
    padding: 15px;
  }
}
