/* About Couple Card Styles */
.about-couple-card {
  display: flex;
  justify-content: space-between;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 30px;
  margin: 30px 0;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15); /* Increased shadow for more depth */
  flex-wrap: wrap; /* Ensures wrapping on smaller screens */
}

/* About Section for Groom and Bride */
.about-section {
  display: flex;
  flex: 1;
  align-items: flex-start; /* Align items to the start */
  justify-content: space-between;
  padding: 0 20px;
}

.about-section.groom {
  border-right: 1px solid #ddd;
  padding-right: 30px;
}

.about-section.bride {
  border-left: 1px solid #ddd;
  padding-left: 30px;
}

/* Text Styles for Groom and Bride */
.about-text {
  flex: 1;
  margin-right: 30px;
}

.about-text h2 {
  font-family: 'Playfair Display', serif;
  font-size: 2.5em; /* Increased font size */
  color: #D35400;
  margin-bottom: 15px;
}

.about-text p {
  font-size: 1.2em; /* Increased font size */
  color: #555;
  line-height: 1.8; /* Increased line-height for better readability */
  margin: 0;
}

/* Image Styles */
.about-image {
  flex: 0 0 auto; /* Prevent image from growing to fill the container */
  margin-left: 20px; /* Space between text and image */
}

.about-image img {
  width: 250px; /* Increased image width */
  height: 200px; /* Adjusted image height */
  border-radius: 8px; /* Slightly rounded corners */
  object-fit: cover;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15); /* More shadow for a pronounced effect */
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .about-couple-card {
    flex-direction: column; /* Stack sections vertically */
    padding: 20px;
  }

  .about-section {
    flex-direction: column; /* Stack text and image vertically */
    align-items: center; /* Center align content */
    padding: 0; /* Remove padding */
    border: none; /* Remove borders for a cleaner look */
    margin-bottom: 20px; /* Space between sections */
  }

  .about-section.groom {
    border-bottom: 1px solid #ddd; /* Border for separation */
    margin-bottom: 20px; /* Space between sections */
  }

  .about-section.bride {
    border-top: 1px solid #ddd; /* Optional: add if you want a border for the bride section */
  }

  .about-text {
    margin-right: 0; /* Remove margin on mobile */
    text-align: center; /* Center text on mobile */
    margin-bottom: 20px; /* Space between text and image */
  }

  .about-text h2 {
    font-size: 2rem; /* Slightly smaller heading on mobile */
  }

  .about-text p {
    font-size: 1.1rem; /* Slightly smaller paragraph text */
  }

  .about-image {
    margin-left: 0; /* Remove margin on mobile */
    margin-top: 10px; /* Space above the image */
  }

  .about-image img {
    width: 300px; /* Larger image on mobile */
    height: 250px; /* Adjusted height for a larger appearance */
  }
}
